.header {
  background-image: url(/images/header/Cover_photo.png);
  background-size: cover;
  background-position: right;

  &__content {
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    margin: 0;
    color: $c-white;
    text-align: center;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 100%;
    letter-spacing: -1.5px;
  
    @include onTablet {
      font-size: 64px;
    }
  
    @include onDesktop {
      font-size: 80px;
    }
  }
  
  &__bottom {
    padding-bottom: 88px;
  
    @include onTablet {
      padding-bottom: 64px;
    }
  
    @include onDesktop {
      padding-bottom: 203px;
    }
  }
}
